import { Store } from '@ngrx/store';
import { NowPlaylistService, UserProfile } from '@core/services';
import * as NowPlaylist from '@core/store/now-playlist';
import * as AppPlayer from '@core/store/app-player';
var UserPlayerService = /** @class */ (function () {
    function UserPlayerService(nowPlaylistService, userProfile, store) {
        this.nowPlaylistService = nowPlaylistService;
        this.userProfile = userProfile;
        this.store = store;
    }
    UserPlayerService.prototype.playSelectedPlaylist = function (playlist) {
        var _this = this;
        this.userProfile
            .fetchPlaylistItems(playlist.id, '')
            .subscribe(function (items) {
            _this.store.dispatch(new NowPlaylist.QueueVideos(items));
            _this.nowPlaylistService.updateIndexByMedia(items[0].id);
            _this.store.dispatch(new AppPlayer.LoadAndPlay(items[0]));
        });
    };
    UserPlayerService.prototype.queuePlaylist = function (playlist) {
        var _this = this;
        this.userProfile
            .fetchPlaylistItems(playlist.id, '')
            .subscribe(function (items) {
            _this.store.dispatch(new NowPlaylist.QueueVideos(items));
            return items;
        });
    };
    UserPlayerService.prototype.queueVideo = function (media) {
        this.store.dispatch(new NowPlaylist.QueueVideo(media));
    };
    UserPlayerService.prototype.playVideo = function (media) {
        this.store.dispatch(new AppPlayer.LoadAndPlay(media));
        this.store.dispatch(new NowPlaylist.QueueVideo(media));
        this.store.dispatch(new NowPlaylist.SelectVideo(media));
    };
    return UserPlayerService;
}());
export { UserPlayerService };



